.team-page {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-top: 130px;
}

.div-sec {
  display: flex;
  flex-direction: column;
  margin: 5px;
  padding: 10px 30px;
  position: relative;
}


.next {
  font-size: 70px;
  position: absolute;
  bottom: 25%;
  cursor: pointer;
  color: black;
  right: 4%;
}
.prev {
  font-size: 70px;
  position: absolute;
  bottom: 25%;
  left: 4%;
  cursor: pointer;
  color: black;
}


.div-sec > p {
  line-height: 20px;
  font-weight: bold;
  font-family: Source Code Pro;
  text-justify: justify;
  font-size: 18px;
  height: 20%;
  text-align: left;
  color: #3C3C3C;
}
.desc > h1 {
  margin-bottom: -10px;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Russo One", sans-serif;
  margin-top: -15px;
  font-size: 70px;
  color: #3c3c3c;
}
.desc > h3 {
  margin-top: -5px;
  color: #7c5e49;
  font-size: 70px;
  font-family: "Russo One", sans-serif;
}

.desc {
  margin-top: 15px;
  position: relative;
  box-sizing: border-box;
  height: 50%;
}
.iconImg {
  width: 650px;
  height: 650px;
  margin-top: -90px;
}
.iconImg {
  width: 650px;
  height: 650px;
  margin-top: -90px;
}

