@font-face {
  font-family: Imported;
  src: url(../../fonts/Flood\ Std\ Regular.otf);
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  line-height: 17px;
  font-weight: normal;
  font-size: 20px;
  padding-bottom: 0;
  margin-bottom: 0;
  font-family: "Russo One", sans-serif;
  padding: 10px 10px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 200;
  background-color: #EDEDED;
}

.nav-items > ul {
  display: flex;
}
.nav-items > ul > li {
  list-style-type: none;
  margin: 10px;
  cursor: pointer;
  margin-right: 10px;
  color: #3c3c3c;
}
.nav1 {
  list-style-type: none !important;

  cursor: pointer;
  text-decoration: none;
  color: #3c3c3c;
}

.comingSoon {
  list-style-type: none;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #b1abab;
  /* margin-right: 15px; */
  cursor: pointer;
  position: relative;
}
.comingSoon > p {
  position: absolute;
  color: red;
  font-weight: bold;
  transform: rotate(-4deg);
  font-size: 15px;
  cursor: pointer;
  font-family: Imported;
  opacity: 0.4;
  top: -20px;
 
}
.socialIcons {
  margin-right: 15px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}


