.homepage {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-top: 130px;
}
.details-side {
  flex: 8;
  box-sizing: border-box;
  position: relative;
  text-align: left;
  padding-left: 130px;
  line-height: 32px;
  font-family: "Source Code Pro", monospace;
}
.smallnft {
  width: 150px;
  height: 150px;
  position: absolute;
  left: -20px;
  bottom: -45px;
}
.image-side {
  flex: 4;
  position: relative;
}

.img1 {
  width: 350px;
  height: 400px;
  position: absolute;
  right: 100px;
  z-index: 2;
  bottom: -45px;
}

.img2 {
  width: 300px;
  height: 350px;
  position: absolute;

  bottom: -45px;
  right: 0;
  z-index: 1;
}

.details-side > p {
  line-height: 18px;
  font-weight: bold;
  font-family: Source Code Pro;
  margin-top: -5px;
}

.lbs1 {
  color: #7c5e49;
  font-weight: bold;
  font-size: 70px;
  font-family: "Russo One", sans-serif;
}

.baboon1{
  margin-bottom: -10px;
  font-size: 70px; 
  font-weight: bold;
  font-family: "Russo One", sans-serif;

}
.minting {
  background-color: #ce7279;
  padding: 8px 14px;
  border: none;
  outline: none;
  margin-right: 18px;
  border-radius: 5px;
  /* font-weight: bold; */
  color: #fff;
  font-family: "Russo One", sans-serif;
  font-size: 20px;
  
  cursor: pointer;
}
.lore {
  font-size: 20px;
  cursor: pointer;
  color: #ce7279;
  background-color: #000;
  padding: 8px 14px;
  border: none;
  outline: none;
  margin-right: 10px;
  border-radius: 5px;
  /* font-weight: bold; */
  font-family: "Russo One", sans-serif;
}

