.roadMap {
  display: flex;
  flex-direction: row;
  margin-top: 100px;
}

.scrollBar {
  position: fixed;
  padding: 20px;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin-top: 50px;
}

.scrollBar > ul {
  list-style-type: none;
  text-decoration: none;
  color: #fff;
}
.scrollBar > ul > li {
  width: 30px;
  height: 30px;
  margin-bottom: 30px;
}
.selected {
  text-decoration: none;
  font-size: 2rem;
  border-radius: 50%;
  background-color: #7c5e49;
  color: white;
  padding: 20px;
  font-weight: bold;
}
.scrollBar > ul > li:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.phases {
  flex: 8;
  margin-left: 150px;
}
.ico {
  text-decoration: none;
  font-size: 2rem;
  border-radius: 50%;
  padding: 20px;
  font-weight: bold;
  border: 1px solid #7c5e49;
  color: #000;
  list-style-type: none;
}

