.phas1 {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-top: 40px;
  height: 100vh;
  /* margin-top: 120px; */
  

}

.P1_lbs {
  color: #7c5e49;
  font-size: 2rem;
  font-family: "Russo One", sans-serif;
}
.P1_baboon {
  margin-bottom: -10px;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Russo One", sans-serif;
  color:#3c3c3c;
}

.P1_details-side {
  flex: 8;
  box-sizing: border-box;
  position: relative;
  text-align: left;
  padding-left: 100px;
  padding-top: 50px;
  padding-right: 50px;
  font-family: "Source Code Pro", monospace;
}
.P1_details-side > p {
  line-height: 25px;
  font-weight: bold;
  font-family: Source Code Pro;
  text-align: left;
  color: #3C3C3C;
}

.P1_image-side {
  flex: 4;
  position: relative;
}

.phase1Imag {
  width: 350px;
  height: 350px;
  position: absolute;
  right: 0;
  z-index: 2;
  bottom: 0;
}

.lbs {
  color: #ce7279;
  font-weight: bold;
  font-size: 3rem;
}
.baboon {
  margin-bottom: -10px;
  font-size: 3rem;
  font-weight: bold;
}
.roadmapBtn {
  background-color: #ce7279;
  padding: 8px 35px;
  border: none;
  outline: none;
  margin-right: 18px;
  border-radius: 5px;
  /* font-weight: bold; */
  color: #fff;
  cursor: pointer;
  font-family: "Russo One", sans-serif;
  letter-spacing: 1.5px;
}

