.Lore {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  /* margin-bottom: 40px; */
  margin-top: 130px;
}
.L_details-side {
  flex: 8;
  box-sizing: border-box;
  position: relative;
  text-align: left;
  padding-left: 130px;
  line-height: 32px;
  font-family: "Source Code Pro", monospace;
}
.L_lbs {
  color: #7c5e49;
  font-weight: bold;
  font-size: 3rem;
  font-family: "Russo One", sans-serif;
}
.L_baboon {
  margin-bottom: -10px;
  font-size: 3rem;
  font-weight: bold;
  font-family: "Russo One", sans-serif;
}
.L_details-side > p {
  line-height: 18px;
  font-weight: bold;
  font-family: Source Code Pro;
}

.L_image-side {
  flex: 4;
  position: relative;
}

.loreImg {
  width: 350px;
  height: 400px;
  position: absolute;
  right: 20px;
  z-index: 2;
  bottom: -18px;
}

.L_details-side > p {
  line-height: 20px;
  font-weight: bold;
  font-family: Source Code Pro;
  margin-top: -5px;
}

.L_lbs {
  color: #7c5e49;
  font-weight: bold;
  font-size: 3rem;
}
.L_baboon {
  margin-bottom: -10px;
  font-size: 3rem;
  font-weight: bold;
}
.roadmapBtn {
  background-color: #ce7279;
  padding: 8px 35px;
  border: none;
  outline: none;
  margin-right: 18px;
  border-radius: 5px;
  /* font-weight: bold; */
  color: #fff;
  cursor: pointer;
  font-family: "Russo One", sans-serif;
  letter-spacing: 1.5px;
}

@media only screen and (min-width: 601px) and (max-width: 767px) {
  .Lore {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
  }
  .L_details-side {
    box-sizing: border-box;
    position: relative;
    text-align: left;
    font-family: "Source Code Pro", monospace;
    padding: 20px;
    padding-left: 100px;
    padding-top: 10px;
  }
  .lbs {
    color: #7c5e49;
    font-weight: bold;
    font-size: 60px;
    line-height: 90px;
    font-family: "Russo One", sans-serif;
    margin-top: -20px;
  }
  .baboon {
    font-size: 60px;
    line-height: 60px;
    font-weight: bold;
    font-family: "Russo One", sans-serif;
  }
  .L_details-side {
    padding: 50px;
  }
  .L_details-side > p {
    font-weight: bold;
    font-family: Source Code Pro;
    line-height: 28px;
  }
  .L_image-side > img {
    right: -10px;
  }
  .roadmapBtn {
    background-color: #ce7279;
    border: none;
    outline: none;
    border-radius: 5px;
    color: #fff;
    font-family: "Russo One", sans-serif;
    cursor: pointer;
    width: 180px;
    height: 46px;
    font-size: 18px;
  }
}

