.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #ededed;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.mobile {
  display: hidden;
}

@media only screen and (max-width: 600px) {
  .mobile {
    display: inline;
    font-size: 30px;
    
    font-family: "Russo One", sans-serif;
  }
  .screens {
    display: none;
  }
}
@media only screen and (min-width: 601px) {
  .mobile {
    display: none;
  }
}
@media only screen and (min-width: 768px) {
  .mobile {
    display: none;
  }
}
